import { Button } from "./Button";
import './HeroSection.css';
import '../App.css';
import Carousel from "react-elastic-carousel";
import item from "./item";
import React from 'react';
import "./styles.css";


const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 550, itemsToShow: 2 },
    { width: 768, itemsToShow: 3 },
    { width: 1200, itemsToShow: 4 },
  ];
function HeroSection() {
    return (
        <div className="row">
            <div className="col-8">
                <div className="hero-container">
                    <h6>Invest in founders building the future</h6>
                    <p>Invest as litle as $100 in the startups and small buisnesses you believe in.</p>
                    <div>
                        <Button className='btn' buttonStyle='btn btn-danger' buttonSize='btn-large'>JOIN ADOPT STARTUP</Button>
                    </div>
                </div>
            </div>
            <div className="col-4">
                <div className="car">
                <Carousel breakPoints={breakPoints}>
                    <item>One</item>
                    <item>Two</item>
                    <item>Three</item>
                    <item>Four</item>
                    <item>Five</item>
                    <item>Six</item>
                    <item>Seven</item>
                    <item>Eight</item>
                </Carousel>
                </div>
            </div>
        </div>
    )
}

export default HeroSection
